var mountNode = document.getElementById("app");

const MyComp = props => {
  const handleChange = content => {
    console.log(content);
  };
  return (
    <div>
      <SunEditor onChange={handleChange} />
    </div>
  );
};

ReactDOM.render(<MyComp />, mountNode);
